import React, { useState } from 'react';
import { Navbar, NavbarBrand, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap';
import Foto from './assets/foto.png';

const Header = (props) => {
    const Navbaratas = {
        background: 'transparent',
    }
    const {
        className
    } = props;

    const [modal, setModal] = useState(false);

    const toggle = () => setModal(!modal);
    return (
        <div>
            <Navbar light fixed-top style={Navbaratas} className="animated fadeInDown">
                <NavbarBrand onClick={toggle} className="mr-auto"><img src={Foto} alt="Rahmat Agung Julians" className="fotoatas" /></NavbarBrand>
                <Nav navbar>
                    <NavItem>
                        <NavLink className="Namaatas" onClick={toggle}>Rahmat Agung Julians</NavLink>
                    </NavItem>
                </Nav>
            </Navbar>
            <Modal isOpen={modal} toggle={toggle} className={className}>
                <ModalHeader toggle={toggle}>Tentang Saya</ModalHeader>
                <ModalBody>
                    Halo, terima kasih sudah mengunjungi halaman ini. Perkenalkan nama saya Rahmat Agung Julians
                    , saya membuat aplikasi ini untuk melatih kemampuan saya dalam merancang Front End menggunakan
                    Framework Javascript yaitu React JS.
                </ModalBody>
                <ModalFooter>
                    <Button color="secondary" onClick={toggle}>Tutup</Button>
                </ModalFooter>
            </Modal>
        </div>
    );
}

export default Header;