import React, { Component } from 'react';
import './App.css';
import { Button } from 'reactstrap';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Profil from './component/Profil';
import Karir from './component/Karir';
import Header from './component/Header';
import Lainnya from './component/Lainnya'

class App extends Component {
  render() {
    const TOMBOL = {
      color: '#edf2f7', fontWeight: '600', background: 'none', border: 'none', padding: '0 0.75rem',
      fontSize: '0'
    }
    return (
      <div className="body">
        <Router>
          <Switch>
            <Route exact path="/karir">
              <Header />
              <Karir />
            </Route>
            <Route exact path="/lainnya">
              <Header />
              <Lainnya />
            </Route>
            <Route path="/">
              <Profil />
            </Route>
          </Switch>
          <div className="navbar fixed-bottom" style={{ background: 'white', borderTop: '1px solid rgba(0,0,0,.1)' }}>
            <center className="tengah animated rubberBand">
              <Link to="/"> <Button color="primary" style={TOMBOL}><i class="fas fa-user"></i><br/><label className="judulbawah">PROFIL</label></Button></Link>
              <Link to="/karir"> <Button color="primary" style={TOMBOL}><i class="fas fa-award"></i><br/><label className="judulbawah">KARIR</label></Button></Link>
              <Link to="/lainnya"> <Button color="primary" style={TOMBOL}><i class="fas fa-bars"></i><br/><label className="judulbawah">LAINNYA</label></Button></Link>
            </center>
          </div>
        </Router>
      </div >
    )
  }
}
